<template>
  <div>
    <div align="left">
      <p class="lead">社团活动记录</p>
    </div>
    <br>
    <el-form :model="PostForm" :rules="rules" class="" labelPosition="right">
      <el-form-item label="活动联系人">
        <el-col :span=4>
          <el-form-item>
            <el-select v-model="PostForm.LinkmanGrade" placeholder="请选择年级" value="">
              <el-option label="高一" value="1"></el-option>
              <el-option label="高二" value="2"></el-option>
              <el-option label="高三" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span=4>
          <el-form-item>
            <el-select v-model="PostForm.LinkmanClass" placeholder="请选择班级" value="">
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
              <el-option label="4" value="4"></el-option>
              <el-option label="5" value="5"></el-option>
              <el-option label="6" value="6"></el-option>
              <el-option label="7" value="7"></el-option>
              <el-option label="8" value="8"></el-option>
              <el-option label="9" value="9"></el-option>
              <el-option label="10" value="10"></el-option>
              <el-option label="11" value="11"></el-option>
              <el-option label="12" value="12"></el-option>
              <el-option label="13" value="13"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span=4>
          <el-form-item>
            <el-input v-model="PostForm.LinkmanName" placeholder="请填写姓名"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span=4>
          <el-form-item :required=true>
            <el-input v-model="PostForm.LinkmanPhoneNumber" placeholder="请填写联系电话"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span=4>
          <el-form-item :required=true>
            <el-input v-model="PostForm.LinkmanQq" placeholder="请填写QQ"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="活动地点">
        <el-input v-model="PostForm.Region" type="textarea" placeholder="请输入活动地点"></el-input>
      </el-form-item>
      <el-form-item label="活动时间">
        <el-form-item>
          <el-date-picker v-model="PostForm.Date1" placeholder="请选择日期时间"></el-date-picker>
        </el-form-item>
      </el-form-item>
      <el-form-item label="活动内容">
        <el-input type="textarea" v-model="PostForm.Content" placeholder="请输入活动内容"></el-input>
      </el-form-item>
      <el-form-item label="学习过程">
        <el-input type="textarea" v-model="PostForm.Process" placeholder="请输入学习过程"></el-input>
      </el-form-item>
      <el-form-item label="分析评估">
        <el-input type="textarea" v-model="PostForm.Assessment" placeholder="请输入分析评估"></el-input>
      </el-form-item>
      <el-form-item label="活动感悟">
        <el-input type="textarea" v-model="PostForm.Feeling" placeholder="请输入活动感悟"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm">立即提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import { getCookie } from 'tiny-cookie'
  import axios from 'axios'
  export default {
    data () {
      return {
        PostForm: {
          ClubName: this.GetClubName,
          LinkmanGrade: '',
          LinkmanClass: '',
          LinkmanName: '',
          LinkmanPhoneNumber: '',
          LinkmanQq: '',
          Region: '',
          Date1: '',
          Content: '',
          Process: '',
          Assessment: '',
          Feeling: ''
        },
        Settings: {
          message: ''
        },
        rules: {
          ClubName: [
            {required: true, message: '请输入社团名称', trigger: 'blur'}
          ],
          Region: [
            {required: true, message: '请输入活动区域', trigger: 'blur'}
          ],
          Date1: [
            {type: 'date', required: true, message: '请选择日期', trigger: 'change'}
          ],
          Process: [
            {required: true, message: '请输入学习过程', trigger: 'change'}
          ],
          Assessment: [
            {required: true, message: '请输入分析评估', trigger: 'change'}
          ],
          Content: [
            {required: true, message: '请输入活动内容', trigger: 'change'}
          ],
          Feeling: [
            {min: 200, required: true, message: '请输入活动感受', trigger: 'change'}]
        }
      }
    },
    methods: {
      submitForm () {
        axios({
          method: 'POST',
          url: '/api/club/post/EditSubmit',
          data: JSON.stringify({
            ClubId: this.GetClubId,
            ClubName: this.GetClubName,
            Linkman: this.PostForm.Linkman,
            Region: this.PostForm.Region,
            Date1: this.PostForm.Date1,
            Content: this.PostForm.Content,
            Process: this.PostForm.Process,
            Assessment: this.PostForm.Assessment,
            Feeling: this.PostForm.Feeling,
            Token: this.GetToken
          })
        }).then(function (response) {
          if (response.data.message === 'Error') {
            this.Settings.message = 'Error'
          } else if (response.data.message === 'Success') {
            this.Settings.message = 'Success'
            this.resetForm('PostForm')
          }
        }).catch(function () {
          alert('error: PostEdit')
        })
      },
      GoToPostForm () {
        this.$router.push('/post/list')
      }
    },
    computed: {
      GetClubName () {
        return getCookie('ClubName')
      },
      GetClubId () {
        return getCookie('ClubId')
      },
      GetToken () {
        return getCookie('ClubToken')
      }
    }
  }
</script>
<style>
</style>



